<template>
  <div class="all-container" style="height:auto">

            <div  v-for="(item, $index) in formOpt" class="list-content detail-content" style="padding:0 1em!important;height:calc(100% - 2em)">
                <h5 class="list-header " style="padding-top:0">
                     <i class="hasIcon hasImg"><img :src="item.img" /></i>
                    {{item.name}}
                  <!--item.operate && item.listType === 'list' && controls[2]-->
                    <template v-for="(buttons, $indexBtn) in item.operate" v-if="controls[2]">
                        <button class="list-operate" @click="editSystemInfo" v-if="controls[2]">
                          <i class="hasIcon"><icon :name="buttons.icon"></icon></i>
                          修改
                        </button>
                         <button   class="list-operate" @click="getConfigs">
                           <i class="hasIcon"><icon name="refresh"></icon></i>
                            刷新
                        </button>
                    </template>
                </h5>
                <VuePerfectScrollbar class="form-list" style="height: calc(100% - 6em);padding:2em 2em 4em 2em">
                    <template  v-for="(items, $indexs) in lists[item.list]" v-if="dataReady">
                    <li style="width: calc(33% - 2em);margin: 0.3em 1em;" v-if="items.style != 'other'">
                        <span class="label-span bold-span"  >
                          {{items.name}}：&nbsp;&nbsp;
                        </span>
                        <span  class="text-span label-span" v-html="getValue(items.value)"></span>
                    </li>
                    <li style="width: calc(100% - 2em);margin: 0.3em 1em;" v-if="items.style== 'other'">
                        <span class="label-span bold-span" >
                          {{items.name}}：&nbsp;&nbsp;
                        </span>
                        <div class="massageInfo">
                          <p><span>尊敬的XX用户：</span></p>
                            <p>
                                <span>您的用电地址为XXXX，编号XXXX，截止XXXX，剩余XXXX元，请尽快到</span>
                                <span >{{getValue(items.option[0].value)}}</span>
                                <span>办理缴费业务</span>
                            </p>
                            <p>
                                <span style="float:right" >{{getValue(items.option[1].value)}}</span>

                            </p>
                        </div>
                    </li>
                    </template>
                <!--    <li style="width: calc(100% - 2em);margin: 0.3em 1em;">
                     <span class="label-span bold-span" >二维码：&nbsp;&nbsp;</span>
                     <qrcode-vue :value="qrcodeValue" :size="size" level="H"></qrcode-vue>
                   </li> -->
                </VuePerfectScrollbar>
            </div>

    <dialogs type="loading" ref="loadings"></dialogs>
    <dialogs type="alert" ref="alerts"></dialogs>
    <dialogs type="customer" ref="editSys" width="45em" v-if="showEdit" overflow="auto">
      <div slot="header">编辑系统参数</div>
      <div slot="content" >
        <ul class="dialog-ul">
          <li v-for="(item, $index) in dialogList" class="formList" :class="item.classes">
            <label class="label-show" style="min-width:7.5em"><span v-if="!item.isEmpty" class="emptyTips"> * </span>{{item.name}}</label>
            <template v-if="item.style === 'input'">
              <inputs :ref="item.option.key" :option="item.option" @getInput="getFrom"></inputs>
            </template>
            <template v-if="item.style === 'select'">
              <selects :ref="item.option.key" :option="item.option" @getSelect="getFrom"></selects>
            </template>
          </li>
        </ul>
      </div>
      <div slot="footer">
        <button class="button-default" @click="saveEdit">提交</button>
        <button class="button-default cancel-button" @click="cancelEdit">取消</button>
      </div>
    </dialogs>
  </div>
</template>
<script>
    import QrcodeVue from 'qrcode.vue';
    export default{
        data() {
            return {

                size: 60,
                systemInfo: {
                  sysName: '',
                  frontProcessorIp: '',
                  frontProcessorPort: '',
                  apn: '',
                  balanceDate: '',
                  payAddr: '',
                  companyName: '',
                  webSocketUrl: '',
                  websocketIp: '',
                  websocketPort: '',
                  websocketHt: '',
                  copyright: ''
                },
                controls: [],
                dataReady: true,
                showCJFS: false,
                showEdit: true,
                formOpt: [
                    {
                        name: '系统参数',
                        img: 'dist/img/configuration/system-setting-icon.png',
                        color: '#F59B7C',
                        listType: 'list',
                        list: 'systemSet',
                        operate: [
                            {text: '修改', icon: 'edit', event: 'editSystemInfo'}
                        ]
                    }
                ]
            }
        },
        components: {
           QrcodeVue
        },
        methods: {
            cancelEdit: function () {
              this.$refs.editSys.close();
              this.showEdit = false;
              this.getConfigs();
            },
            getFrom: function (val) {
              this.getOption(val, true);

            },
            saveEdit: function () {
                let loadings = this.$refs.loadings;
                let alerts = this.$refs.alerts;
                let p = this.systemInfo;
                p['userId'] = localStorage.userId;

                let that = this;
                this.regularTest({
                 emptyKey: ['sysName', 'frontProcessorIp', 'frontProcessorPort', 'apn', 'balanceDate', 'payAddr', 'companyName','websocketIp','websocketPort', 'websocketHt'],
                    prefix: 'systemInfo',
                    callback: function () {
                        loadings.show({
                            msg: '参数修改中，请稍后......'
                        });
                        p.webSocketUrl = that.systemInfo.websocketHt + '://' + that.systemInfo.websocketIp + ':' + that.systemInfo.websocketPort;
                        that.getRequest({
                            url: 'system/update',
                            method: 'post',
                            param: p,
                            success: function (data) {
                                let parent = that.$root.$children[0].$children[0];
                               // websocket.pageOver();
                                that.$store.state.socketUrl = localStorage.socketUrl =  p.webSocketUrl;
                                  parent.reStartSocket();
                              //  websocket.initWebSocket();
                                loadings.close();
                                alerts.show({
                                  msg: data.message
                                });
                                that.$store.state.systemConfig  = p;
                                localStorage.systemConfig = JSON.stringify(p);
                                that.cancelEdit();
                                that.$store.state.sysName = localStorage.SystemName = p.sysName;
                            },
                          error: function(data) {
                            loadings.close();
                            alerts.show({
                              msg: data.message
                            });
                          }
                        })
                    }
                })
            },
            editSystemInfo: function () {
             //   this.showEditBtn = !this.showEditBtn;
             //   this.getConfigs();
              let that = this;
              that.showEdit = true;
              that.$nextTick(function () {

                  that.$refs.editSys.show();
              });
            },
            getConfigs: function () {
            	 let that = this;
                that.getRequest({
                              url: 'system/querySystemConfig',
                              method: 'post',
                              success: function (data) {
                                let Data = data.data;
                                localStorage.systemConfig = JSON.stringify(Data);
                                that.$store.state.sysName = Data.sysName;
                                that.$store.state.copyright = localStorage.copyright = Data.copyright;
                                that.$store.state.balanceTime = Data.balanceDate;
                                that.$store.state.socketUrl = localStorage.socketUrl = Data.webSocketUrl;
                                that.$store.state.systemConfig = Data;
                                that.systemInfo = Data;
                              }
                           });
                that.dataReady = false;
                setTimeout(function () {
                    that.dataReady = true;
                }, 0);
            },
            init: function () {
               // this.systemInfo = JSON.parse(localStorage.systemConfig);
                this.getConfigs();
               // console.log(this.systemInfo)
            }
        },
        computed: {
            qrcodeValue: function () {
              let WbUrl = this.systemInfo.webSocketUrl || 'ws://10.10.1.250:8087';
              let urlArr = WbUrl.split(':');
              switch(urlArr[0]) {
                 case 'ws':

                   return WbUrl.replace('ws', 'http') + '/';
                 case 'wss':

                  return WbUrl.replace('ws', 'https') + '/';
              }
            },
            lists: function () {
                return {
                  systemSet: [
                    {
                      name: '系统名称',
                      value: this.systemInfo.sysName
                    },
                    {
                      name: '前置机IP',
                      value: this.systemInfo.frontProcessorIp

                    },
                    {
                      name: '前置机端口',
                      value: this.systemInfo.frontProcessorPort
                    },
                    {
                      name: '前置机状态',

                      value: this.systemInfo.frontProcessorStatus === 1 ? `<span style="color:#68d288">已连接</span>` : `<span style="color:#f4a951">断开</span>`
                    },
                    {
                      name: '长连接地址',

                      value: this.systemInfo.webSocketUrl || 'ws://10.10.1.250:8087'
                    },
                    {
                      name: '长连接状态',

                      value: this.$store.state.socketLink ? `<span style="color:#68d288">已连接</span>` : `<span style="color:#f4a951">断开</span>`
                    },
                    {
                      name: 'APN',
                      value: this.systemInfo.apn

                    },
                    {
                      name: '结算日',
                      value: this.systemInfo.balanceDate,
                    },
                    {
                      name: '版权配置',
                      value: this.systemInfo.copyright
                    },
                    {
                      name: "公司名称",
                      value: this.systemInfo.companyName
                    },
                    {
                      name: "公司地址",
                      value: this.systemInfo.payAddr
                    },
                    {
                      name: '版本号',
                      value: this.$store.state.appVersion
                    }
                    // {
                    //   name: '短信模板',
                    //   style: 'other',
                    //   option: [
                    //   {
                    //     value: this.systemInfo.payAddr,
                    //   },
                    //   {
                    //     value: this.systemInfo.companyName,
                    //   }
                    // ]
                    // }

                  ]
                }
            },
            dialogList: function () {
               let arr = [];
                for (let i=1; i <= 28; i++) {
                    arr.push({text: i + '号', value:  i.toString() })
                }
                let width = 'calc(100% - 8.3em)';
                let WbUrl = this.systemInfo.webSocketUrl || 'ws://10.10.1.250:8087';
                let urlArr = WbUrl.split(':');
                this.systemInfo.websocketHt = urlArr[0];
                this.systemInfo.websocketIp = urlArr[1].replace('//', '');
                this.systemInfo.websocketPort = urlArr[2];
                return [
                   {
                      name: '系统名称',
                      style: 'input',
                      classes: 'halfWidth',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.sysName',
                        width: width,
                        value: this.systemInfo.sysName,
                        type: 'normalInput',
                        length: 20
                      }
                   },
                   {
                      name: '前置机IP',
                      style: 'input',
                      classes: 'halfWidth hasMarginLi',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.frontProcessorIp',
                        width: width,
                        value: this.systemInfo.frontProcessorIp,
                        type: 'normalInput',
                        regular: 'ip',
                        errorMsg: '前置机IP：如10.10.1.250',
                        length: 15
                      }
                    },
                     {
                      name: '前置机端口',
                      style: 'input',
                      classes: 'halfWidth',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.frontProcessorPort',
                        width: width,
                        value: this.systemInfo.frontProcessorPort,
                        type: 'normalInput',
                        placeHolder: '1-65535之间的整数',
                        regular: 'port',
                        errorMsg: '前置机端口：1-65535之间的整数',
                        length: 5
                      }
                    },
                     {
                      name: 'APN',
                      style: 'input',
                      classes: 'halfWidth hasMarginLi',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.apn',
                        width: width,
                        value: this.systemInfo.apn,
                        type: 'normalInput',
                        regular: 'apn',
                        errorMsg: 'APN：大写英文字母',
                        length: 10
                      }
                    },
                     {
                      name: '结算日',
                      style: 'select',
                      classes: 'halfWidth',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.balanceDate',
                        cs: 'client',
                        width: width,
                        data: arr,
                        constant: {text: this.systemInfo.balanceDate + '号', value: this.systemInfo.balanceDate},
                        text: 'text',
                        value: 'value'
                    }
                    },
                   {
                      name: '长连接协议',
                      style: 'select',
                      classes: 'halfWidth hasMarginLi',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.websocketHt',
                        cs: 'client',
                        width: width,
                        data: [{text: 'http (ws)', value: 'ws'}, {text: 'https (wss)', value: 'wss'}],
                        constant: {value: urlArr[0]}
                     }
                    },
                     {
                      name: '长连接ip地址',
                      style: 'input',
                      classes: 'halfWidth',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.websocketIp',
                        width: width,
                        value: urlArr[1].replace('//',''),
                        type: 'normalInput',
                        regular: 'ip',
                        errorMsg: '长连接ip地址：如10.10.1.250',
                        length: 15
                      }
                    },
                     {
                      name: '长连接端口',
                      style: 'input',
                      classes: 'halfWidth hasMarginLi',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.websocketPort',
                        width: width,
                        value: urlArr[2],
                        type: 'normalInput',
                        placeHolder: '1-65535之间的整数',
                        regular: 'port',
                        errorMsg: '前置机端口：1-65535之间的整数',
                        length: 5
                      }
                    },
                    {
                      name: '公司名称',
                      classes: 'allWidth',
                      style: 'input',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.companyName',
                        width: width,
                        value: this.systemInfo.companyName,
                        type: 'normalInput',
                        length: 50
                      }
                    },
                     {
                      name: '公司地址',
                      style: 'input',
                      classes: 'allWidth',
                      isEmpty: false,
                      option: {
                        key: 'systemInfo.payAddr',
                        width: width,
                        value: this.systemInfo.payAddr,
                        type: 'normalInput',
                        length: 50
                      }
                    },
                    {
                      name: '版权配置',
                      style: 'input',
                      classes: 'allWidth',
                      isEmpty: true,
                      option: {
                        key: 'systemInfo.copyright',
                        width: width,
                        value: this.systemInfo.copyright,
                        type: 'normalInput',
                        length: 50
                      }
                    },
                ]
            }
        },
        mounted: function () {
            this.isIn();
            this.getAction();

        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    button.cardOperate {
        padding:5px 12px;
        margin:5px 10px;
        float:left
    }
    div.massageInfo{
        float:left;
        span{
            float:left
        }
        p{
           margin-top:0;
            margin-bottom:10px
        }
        p:after{
            @extend .clearElement
        }
    }
  .bold-span{
    font-weight:600;
    min-width: 7.5em !important;
    line-height:2.5
  }
</style>



